.page-enter{
  opacity: 0.01;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.page-enter-active{
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 300ms ease-in;
}
.page-exit{
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}
.page-exit-active{
  opacity: 0.01;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: all 300ms ease-out;
}
.hover-underline-animation {
  display: inline-block;
  position: relative; 
  font-size: 35px;
  color: #ffffff;
}

.hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.40s ease-out;
  transition: transform 0.40s ease-out;
  transition: transform 0.40s ease-out, -webkit-transform 0.40s ease-out;
}

.hover-underline-animation:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}


.fifth{
  outline: none;
  border:1px solid #777a7c !important;
  height:2.5rem !important;
  color: #f8f8f8;
  position: relative;
  overflow: hidden; 
  border-radius: 50px !important;
  z-index: 1;
  transition: color 550ms ease-in-out;
}
.fifth:after {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transform: translateX(-50%);
  width: 0;
  height: 100%;
  background: #8e44ad;
  z-index: -1;
  transition: width 550ms ease-in-out;
  transition: width 550ms ease-in-out;
}
.fifth:hover {
  border: none !important;
color: #fff;
}
.fifth:hover:after {
width: 120%;
}
