.accordian{
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding:4px 15px;
    display: flex;
    align-items: center;
    border: none;
    outline: none;
    transition: background-color 0.6s ease;
}
.accordian:hover,
.accordian.active{
    border-left-style: solid;
    border-left-color: rgb(207, 92, 50);
    border-left-width: 6px;
    background-color: #ccc;
}
